
/* Article headline */ h1 {
  font-size: 2.0rem;
  line-height: 1.2;
  margin-top: var(--root-line-height-20x); margin-bottom: var(--root-line-height-25x);
}
@media screen and (max-width: 500px) { h1 {} } 
@media screen and (max-width: 400px) { h1 {font-size: 1.9rem; margin-bottom: var(--root-line-height-20x)} } 

.kicker + h1,
kicker + h1 {margin-top:0;}  
figure + h1,
img + h1 {margin-top:0;margin-bottom:1.3rem;}   
h1+figure {margin-top: calc(-1.5 * var(--root-line-height));}

/* Section headline */ h2 {
  font-size:1.5rem;
  line-height:1.3em;
  margin-top: var(--root-line-height-15x); margin-bottom: var(--root-line-height-075x);
  font-weight: 500;
}
@media screen and (max-width: 500px) { h2 {font-size: 1.4rem;} }

/* Subsection headline */ h3 {
  font-size:0.9rem;
  line-height: var(--root-line-height);
  margin-top: var(--root-line-height-10x); margin-bottom: var(--root-line-height-05x);
  text-transform: uppercase;
  letter-spacing: 0.10em;
  font-weight: 500;
}
@media screen and (max-width: 500px) { h3 {font-size:0.85rem; margin-top: var(--root-line-height);} } 

.container h1+p, .container h2+p, .container h3+p {margin-top: var(--root-line-height-05x);}

/* Over-paragraph headline */ h4 {
  font-size: 1.0rem;
  line-height: var(--root-line-height);
  margin-top: var(--root-line-height-075x); margin-bottom: var(--root-line-height-025x);
  letter-spacing: 0.01em;
  font-weight: 500;
}
@media screen and (max-width: 500px) { h4 {font-size:0.95rem;} } 

.container h4+p {margin-top: 0;}

.container h1+figure, .container h2+figure, .container h3+figure, .container h4+figure {margin-top: var(--root-line-height-10x);}
.container h1+table, .container h2+table, .container h3+table, .container h4+table {margin-top: var(--root-line-height-10x);}
.container h1+*, .container h2+*, .container h3+*, .container h4+* {margin-top: var(--root-line-height-05x);}

h2+figure, h3+figure, h4+figure {margin-top: var(--root-line-height-10x);}
h2+table, h3+table, h4+table {margin-top: var(--root-line-height-10x);}

/* Paragraph headline */ h5 {
}

/* Subparagraph headline */ h6 {
  color: #666;
}

h5, h6 {
  /* On same line */
  float: left;
  padding-right: var(--root-line-height-05x) !important;
  margin-right: 0 !important; /* Reset print */
  margin-bottom: 0  !important;
  font-size: 0.95rem;
  line-height: 1.117rem;
  font-weight: 500;
}

h5::after, h6::after {
  content: ".";
}

h5+p, h6+p { clear: none; }
h5+*, h6+* { clear: both; }

h5, h6 {margin-top: 0;}
p+h5, h5+p, p+h6, h6+p {margin-top: var(--root-line-height) !important;}
