@media print {
  body, #pagepage, #edit, .container {margin: 0; padding: 0;}
  
  :root {
    --list-margin-right: 0rem;
  }
  
  @page {
    margin-top: 3.17cm;
    margin-bottom: 3.17cm;
    margin-left: 3.17cm;
    margin-right: 3.17cm;
  }
  
  @page {
    padding: 0;
    orphans: 2;
    widows: 2;
  }
  
  .hideprint {display:none;}
  .container > figure, .container > table {width: 100%; float:left;}
  
  .container > * {margin-left: 0; margin-right: 3.17cm;}
  body .container > mjx-container[jax="CHTML"][display="true"],
  body .container > mjx-container[jax="SVG"][display="true"] {margin-left: 0; margin-right: 3.17cm;}  /* Overrite overriding */
  
  .container > ol,
  .container > ul {padding-left: 1.95rem;padding-right: 1.95rem;}
  .container > figure, 
  .container > table {box-sizing: border-box; margin-right: 0; padding-right: 3.17cm;}
  .container > table,
  .container > div.highlighter-rouge,
  .container > pre,
  .container > .fullwidth  {padding-right: 0;}
  .container > .fullwidth figcaption {padding-right: 3.17cm;}
  
  .container > figure.float-left {max-width:30%; padding-left: 0;}
  .container > figure.float-right {max-width:30%; padding-right: 0;}
  
  figure, table, a {page-break-inside: avoid;}
  h1, h2, h3, h4, h5, h6 {page-break-inside: avoid; page-break-after: avoid;}
  
  h1, h2, h3 {clear: both;}
  
  /* 
  --Hack--
  page-break-after: avoid does not seems to work in Chrome. This hack fixes it. 
  Source: https://stackoverflow.com/questions/9238868/how-do-i-avoid-a-page-break-immediately-after-a-heading
  */
  img::after, caption::after, h1::after, h2::after, h3::after, h4::after, h5::after, h6::after {
    content: "";
    display: block;
    height: 9.1rem;
    margin-bottom: -9.1rem;
  }
}