// -----------------------------------------------------------------------------
// @font-face declarations
// -----------------------------------------------------------------------------


// Minion Pro
// ----------
// Serif typeface used e.g. on body text, h1 and h2

@font-face{font-display: block;font-family:'Minion Pro';font-weight:400;src:url("/assets/fonts/Minion Pro/Minion Pro Regular.woff2");}
@font-face{font-display: block;font-family:'Minion Pro';font-weight:400;font-style:italic;src:url("/assets/fonts/Minion Pro/Minion Pro Regular Italic.woff2");}
@font-face{font-display: block;font-family:'Minion Pro';font-weight:700;src:url("/assets/fonts/Minion Pro/Minion Pro Semibold.woff2");}
@font-face{font-display: block;font-family:'Minion Pro';font-weight:700;font-style:italic;src:url("/assets/fonts/Minion Pro/Minion Pro Semibold Italic.woff2");}

// Gill sans
// ---------
// Sans-serif typeface used on interface and h3–h6 headers

@font-face{font-display: block;font-family:'Gill Sans';font-weight:200;src:url("/assets/fonts/Gill Sans/GillSans-Light.woff2");}
@font-face{font-display: block;font-family:'Gill Sans';font-weight:200;font-style:italic;src:url("/assets/fonts/Gill Sans/GillSans-LightItalic.woff2");}

@font-face{font-display: block;font-family:'Gill Sans';font-weight:500;src:url("/assets/fonts/Gill Sans/GillSans.woff2");}
@font-face{font-display: block;font-family:'Gill Sans';font-weight:500;font-style:italic;src:url("/assets/fonts/Gill Sans/GillSans-Italic.woff2");}

@font-face{font-display: block;font-family:'Gill Sans';font-weight:600;src:url("/assets/fonts/Gill Sans/GillSans-SemiBold.woff2");}
@font-face{font-display: block;font-family:'Gill Sans';font-weight:600;font-style:italic;src:url("/assets/fonts/Gill Sans/GillSans-SemiBoldItalic.woff2");}

@font-face{font-display: block;font-family:'Gill Sans';font-weight:700;src:url("/assets/fonts/Gill Sans/GillSans-Bold.woff2");}
@font-face{font-display: block;font-family:'Gill Sans';font-weight:700;font-style:italic;src:url("/assets/fonts/Gill Sans/GillSans-BoldItalic.woff2");}

@font-face{font-display: block;font-family:'Gill Sans';font-weight:800;src:url("/assets/fonts/Gill Sans/GillSans-UltraBold.woff2");}

// Lucida Grande
// -------------
// Used for move-up arrow in the footer.

@font-face{font-display: block;font-family:'Lucida Grande';font-weight:500;src:url("/assets/fonts/Lucida Grande/LucidaGrande.woff2");}
@font-face{font-display: block;font-family:'Lucida Grande';font-weight:700;src:url("/assets/fonts/Lucida Grande/LucidaGrande-Bold.woff2");}