
.kicker {display:block; font-size:0.85rem; line-height: var(--root-line-height); font-weight:normal; color: #666;}

.lead,
.big {display:block; font-size: 1.15rem; line-height: var(--relative-line-height); margin-top: var(--root-line-height-05x); margin-bottom: var(--root-line-height-05x); font-style: italic;}

.small {display:block; font-size:0.85rem; line-height: var(--root-line-height); color:#999;}

.author-date {
  margin-top: var(--root-line-height-05x);
  margin-bottom: 0.2rem;
  font-weight: 500;
  font-size: var(--root-line-height-05x);
  line-height: var(--root-line-height);
  text-transform: uppercase;
  font-variant-numeric: normal;
  font-family: Gill Sans, sans-serif;
  letter-spacing: 0.02rem;
  color:#666;
}

.author-date + p {
    margin-top: 0;
}
